//  config 
import config from "../config.js";
// let config = {
// 	// API
// 	upload_img_url:'http://www.test.com/index.php/index/upload'
// }
class Uploader {
	constructor() {
		
	}
	choose(num) {
		return new Promise((resolve, reject) => {
			uni.chooseImage({
				count: num,
				success(res) {
					// console.log(res);
					// 
					resolve(res.tempFilePaths)
				},
				fail(err) {
					console.log(err)
					reject(err)
				}
			})
		})

	}
	upload_one(path) {
		return new Promise((resolve, reject) => {
			uni.showLoading({
				title:''
			})
			uni.uploadFile({
				url: config.upload_img_url, //
				filePath: path,
				name: 'file',
				formData:{
					width:'200',
					height:'200'
				},
				success: (uploadFileRes) => {
					if("string"===typeof uploadFileRes.data){
						resolve(JSON.parse(uploadFileRes.data).data)
					}else{
						resolve( uploadFileRes.data.data )
					}
					
				},
				complete() {
					uni.hideLoading()
				}
			});
		})
	}
	upload(path_arr) {
		let num = path_arr.length;
		return new Promise(async (resolve, reject) => {
			let img_urls = []
			for (let i = 0; i < num; i++) {
				let img_url = await this.upload_one(path_arr[i]);
				console.log(img_url)
				img_urls.push(img_url)
			};
			console.log("")
			resolve(img_urls)
		})


	}
	choose_and_upload(num) {
		return new Promise(async (resolve, reject) => {
			let path_arr = await this.choose(num);
			let img_urls = await this.upload(path_arr);
			resolve(img_urls);
		})

	}
}
export default Uploader;
